// src/components/Sidebar.js
import React, { useEffect, useState } from 'react';
import { Link, useNavigate, useLocation } from 'react-router-dom';
import './Sidebar.css';

function Sidebar({ menuOpen, toggleMenu, handleLogout }) {
  const [user, setUser] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    try {
      const storedUser = localStorage.getItem('user');
      if (storedUser) {
        setUser(JSON.parse(storedUser));
      }
    } catch (error) {
      console.error("Error parsing user data:", error);
      localStorage.removeItem('user');
    }
  }, []);

  const isAdmin = user?.email === 'SkybridgeFlights@gmail.com';
  const isCustomer = user && !isAdmin;

  const logout = () => {
    localStorage.removeItem('user');
    setUser(null);
    handleLogout?.();
    navigate('/login');
  };

  const showBackButton = !['/', '/login', '/register', '/staff-login'].includes(location.pathname);

  const handleGoBack = () => {
    navigate(-1);
    toggleMenu();
  };

  return (
    <aside className={`sidebar ${menuOpen ? 'open' : ''}`}>
      {showBackButton && (
        <button className="back-button" onClick={handleGoBack}> Back</button>
      )}
      <nav className="sidebar-nav">
        <ul>
          {user && (
            <li className="sidebar-user">
              <div className="user-info">
                <strong>{user.name || "User"}</strong>
                <span>{user.email}</span>
              </div>
            </li>
          )}
          <li><Link to="/" onClick={toggleMenu}>Home</Link></li>
          <li><Link to="/flights" onClick={toggleMenu}>Search Flights</Link></li>
          {isCustomer && (
            <>
              <li><Link to="/user-profile" onClick={toggleMenu}>My Profile</Link></li>
              <li><Link to="/bookings" onClick={toggleMenu}>My Bookings</Link></li>
              <li><Link to="/visa" onClick={toggleMenu}>Visa Application</Link></li>
            </>
          )}
          <li><Link to="/about" onClick={toggleMenu}>About Us</Link></li>
          <li><Link to="/contact" onClick={toggleMenu}>Contact Us</Link></li>
          {!user && (
            <>
              <li><Link to="/login" onClick={toggleMenu}>Login</Link></li>
              <li><Link to="/register" onClick={toggleMenu}>Register</Link></li>
              <li><Link to="/staff-login" onClick={toggleMenu}>Staff Login</Link></li>
            </>
          )}
          {isAdmin && (
            <>
              <li><Link to="/admin" onClick={toggleMenu}>Admin Panel</Link></li>
              <li><Link to="/admin/manage-staff" onClick={toggleMenu}>Manage Staff</Link></li>
              <li><Link to="/admin/view-visas" onClick={toggleMenu}>Visa Applications</Link></li>
              <li><Link to="/admin/view-bookings" onClick={toggleMenu}>All Bookings</Link></li>
            </>
          )}
          {user && (
            <li>
              <button onClick={logout} className="logout-button">Logout</button>
            </li>
          )}
        </ul>
      </nav>
    </aside>
  );
}

export default Sidebar;