// src/pages/ConfirmBookingPage.js
import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import './ConfirmBooking.css';
import SeatModal from '../components/SeatModal';
import PetDetailsModal from '../components/PetDetailsModal';

const ConfirmBookingPage = () => {
  const { flightId } = useParams();
  const navigate = useNavigate();

  const [flight, setFlight] = useState(null);
  const [bookedSeats, setBookedSeats] = useState([]);
  const [selectedSeat, setSelectedSeat] = useState(null);
  const [showSeatModal, setShowSeatModal] = useState(false);
  const [showPetModal, setShowPetModal] = useState(false);

  const [form, setForm] = useState({
    firstName: '',
    lastName: '',
    fatherName: '',
    gender: '',
    birthDate: '',
    email: '',
    passport: '',
    country: '',
    city: '',
    street: '',
    zip: '',
  });

  const [extraWeight, setExtraWeight] = useState(0);
  const [baseWeight, setBaseWeight] = useState(20);
  const [extraFee, setExtraFee] = useState(0);
  const [seatFee, setSeatFee] = useState(0);
  const [petFee, setPetFee] = useState(0);
  const [finalPrice, setFinalPrice] = useState(0);
  const [paymentMethod, setPaymentMethod] = useState('');
  const [extraPricePer5Kg, setExtraPricePer5Kg] = useState(10);
  const [petDetails, setPetDetails] = useState(null);
  const [validationErrors, setValidationErrors] = useState({});

  //    
  useEffect(() => {
    const fetchFlight = async () => {
      try {
        const res = await axios.get(`http://localhost:5000/api/flights/${flightId}`);
        const flightData = res.data;
        setFlight(flightData);

        let allowedWeight = 20, extraPer5kg = 10, seatPrice = 5;
        if (flightData.airline === 'Lufthansa') {
          allowedWeight = 25; extraPer5kg = 15; seatPrice = 8;
        } else if (flightData.airline === 'Emirates') {
          allowedWeight = 30; extraPer5kg = 20; seatPrice = 10;
        } else if (flightData.airline === 'Skybridge Airlines') {
          allowedWeight = 23; extraPer5kg = 12; seatPrice = 7;
        }

        if (flightData.class === 'Business') allowedWeight += 10;
        if (flightData.class === 'First Class') allowedWeight += 20;

        setBaseWeight(allowedWeight);
        setExtraPricePer5Kg(extraPer5kg);
        setSeatFee(seatPrice);
        setFinalPrice(flightData.price);
      } catch (err) {
        console.error(' Failed to fetch flight:', err);
      }
    };

    const fetchBookedSeats = async () => {
      try {
        const res = await axios.get(`http://localhost:5000/api/bookings`);
        const seats = res.data
          .filter((b) => {
            if (typeof b.flightId === 'object' && b.flightId !== null) {
              return b.flightId._id === flightId;
            } else {
              return b.flightId === flightId;
            }
          })
          .map((b) => b.seatNumber);
        setBookedSeats(seats);
      } catch (err) {
        console.error(' Failed to fetch booked seats:', err);
      }
    };

    fetchFlight();
    fetchBookedSeats();
  }, [flightId]);

  useEffect(() => {
    let total = flight?.price || 0;
    const extra = (extraWeight / 5) * extraPricePer5Kg;
    const seat = selectedSeat ? seatFee : 0;
    const pet = petDetails ? 25 : 0;

    setExtraFee(extra);
    setPetFee(pet);
    setFinalPrice(total + extra + seat + pet);
  }, [extraWeight, flight, selectedSeat, petDetails, extraPricePer5Kg, seatFee]);

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  //     
  const handleSubmit = async () => {
    const requiredFields = [
      'firstName', 'lastName', 'fatherName', 'gender', 'birthDate',
      'email', 'passport', 'country', 'city', 'street', 'zip'
    ];

    const errors = {};
    requiredFields.forEach(field => {
      if (!form[field]) errors[field] = true;
    });
    if (!paymentMethod) errors.paymentMethod = true;

    setValidationErrors(errors);
    if (Object.keys(errors).length > 0) {
      alert('Please fill all required fields.');
      return;
    }

    try {
      const bookingPayload = {
        ...form,
        flightId,
        seatNumber: selectedSeat,
        extraWeight,
        totalPrice: finalPrice,
        paymentMethod,
        petDetails,
      };

      const res = await axios.post('http://localhost:5000/api/bookings', bookingPayload);
      const bookingId = res.data.bookingId;

      //        
      let freshFlight = flight;
      if (!flight || !flight.from || !flight.to || !flight.airline) {
        const flightRes = await axios.get(`http://localhost:5000/api/flights/${flightId}`);
        freshFlight = flightRes.data;
      }

      navigate(`/payment/${bookingId}`, {
        state: {
          bookingData: {
            _id: bookingId,
            ...bookingPayload,
            ...freshFlight
          }
        }
      });
    } catch (error) {
      console.error(' Booking failed:', error);
      alert('Booking failed. Please try again.');
    }
  };

  if (!flight) return <p className="text-center mt-5">Loading flight details...</p>;

  return (
    <div className="confirm-booking-container">
      <div className="booking-card">
        <h2 className="section-title"> Confirm Your Booking</h2>
        <p className="flight-info-summary">
          <strong>{flight.airline}</strong> | {flight.from}  {flight.to} | <strong>Class:</strong> {flight.class}
        </p>

        <div className="form-grid">
          {Object.entries({
            firstName: 'First Name',
            lastName: 'Last Name',
            fatherName: "Father's Name",
            gender: 'Gender',
            birthDate: 'Date of Birth',
            email: 'Email',
            passport: 'Passport',
            country: 'Country',
            city: 'City',
            street: 'Street',
            zip: 'Zip Code'
          }).map(([key, label]) => (
            <div className="form-group" key={key}>
              <label>{label} <span style={{ color: 'red' }}>*</span></label>
              {key === 'gender' ? (
                <select name={key} value={form[key]} onChange={handleChange}
                        className={validationErrors[key] ? 'error-field' : ''}>
                  <option value="">Select</option>
                  <option>Male</option>
                  <option>Female</option>
                </select>
              ) : (
                <input
                  name={key}
                  type={key === 'birthDate' ? 'date' : 'text'}
                  value={form[key]}
                  onChange={handleChange}
                  className={validationErrors[key] ? 'error-field' : ''}
                />
              )}
            </div>
          ))}

          <div className="form-group">
            <label>Extra Baggage</label>
            <select value={extraWeight} onChange={(e) => setExtraWeight(Number(e.target.value))}>
              <option value={0}>No extra</option>
              <option value={5}>+5 kg ({extraPricePer5Kg})</option>
              <option value={10}>+10 kg ({extraPricePer5Kg * 2})</option>
              <option value={15}>+15 kg ({extraPricePer5Kg * 3})</option>
            </select>
            <small>Included: {baseWeight} kg</small>
          </div>
        </div>

        <div className="options-section">
          <button className="option-button" onClick={() => setShowSeatModal(true)}>
             Choose Seat {selectedSeat && `(${selectedSeat})`}
          </button>
          <button className="option-button" onClick={() => setShowPetModal(true)}>
             Add Pet Details
          </button>
        </div>

        <div className="payment-section">
          <label> Payment Method <span style={{ color: 'red' }}>*</span></label>
          <select value={paymentMethod} onChange={(e) => setPaymentMethod(e.target.value)}
                  className={validationErrors.paymentMethod ? 'error-field' : ''}>
            <option value="">Select</option>
            <option value="visa">Visa</option>
            <option value="paypal">PayPal</option>
          </select>
        </div>

        <div className="price-summary">
          <h4> Price Breakdown</h4>
          <p>Flight: {flight.price}</p>
          <p>Extra Baggage: {extraFee}</p>
          <p>Seat: {selectedSeat ? seatFee : 0}</p>
          <p>Pet: {petFee}</p>
          <hr />
          <h3>Total: {finalPrice}</h3>
        </div>

        <div className="confirm-button-container">
          <button className="confirm-button" onClick={handleSubmit}>
             Continue to Payment
          </button>
        </div>
      </div>

      <SeatModal
        show={showSeatModal}
        onClose={() => setShowSeatModal(false)}
        bookedSeats={bookedSeats}
        selectedSeat={selectedSeat}
        onSelectSeat={setSelectedSeat}
        flightClass={flight.class}
      />

      <PetDetailsModal
        show={showPetModal}
        onClose={() => setShowPetModal(false)}
        petDetails={petDetails}
        setPetDetails={setPetDetails}
      />
    </div>
  );
};

export default ConfirmBookingPage;