import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';

const BookingsPage = () => {
  const [bookings, setBookings] = useState([]);

  useEffect(() => {
    const fetchBookings = async () => {
      try {
        const res = await axios.get('http://localhost:5000/api/bookings');
        setBookings(res.data);
      } catch (error) {
        console.error('Error fetching bookings:', error);
      }
    };
    fetchBookings();
  }, []);

  const handleCancel = async (booking) => {
    const flightDate = new Date(booking.flightId?.date);
    const now = new Date();
    const hoursDiff = (flightDate - now) / (1000 * 60 * 60); //  

    if (hoursDiff < 24) {
      alert(' Cannot cancel booking: Less than 24 hours before flight.');
      return;
    }

    if (window.confirm('Are you sure you want to cancel this booking?')) {
      try {
        await axios.delete(`http://localhost:5000/api/bookings/${booking._id}`);
        setBookings((prev) => prev.filter((b) => b._id !== booking._id));
        alert(' Booking cancelled successfully.');
      } catch (error) {
        alert('Error cancelling booking: ' + (error.response?.data?.error || error.message));
      }
    }
  };

  return (
    <div className="container mt-4">
      <h2> My Bookings</h2>
      {bookings.length === 0 ? (
        <p>No bookings found.</p>
      ) : (
        <table className="table mt-3 table-bordered table-hover">
          <thead className="table-primary">
            <tr>
              <th>Name</th>
              <th>Flight ID</th>
              <th>Date of Flight</th>
              <th>Seat</th>
              <th>Extra Weight</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {bookings.map((b) => (
              <tr key={b._id}>
                <td>{b.name}</td>
                <td>{b.flightId?._id || b.flightId}</td>
                <td>
                  {b.flightId?.date
                    ? new Date(b.flightId.date).toLocaleDateString()
                    : 'N/A'}
                </td>
                <td>{b.seatNumber || 'N/A'}</td>
                <td>{b.extraWeight || 0} kg</td>
                <td>
                  <Link to={`/ticket/${b._id}`} className="btn btn-sm btn-info me-2">
                    View Ticket
                  </Link>
                  <button className="btn btn-sm btn-danger" onClick={() => handleCancel(b)}>
                    Cancel
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
};

export default BookingsPage;