// frontend/src/pages/TicketPage.js
import React, { useEffect, useState, useRef } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import { QRCodeCanvas } from 'qrcode.react';
import { API_BASE_URL } from '../apiConfig'; //    
import './TicketPage.css';

const TicketPage = () => {
  const { bookingId } = useParams();
  const [booking, setBooking] = useState(null);
  const [flight, setFlight] = useState(null);
  const printRef = useRef();

  useEffect(() => {
    if (!bookingId) return;

    const fetchData = async () => {
      try {
        //    
        const bookingRes = await axios.get(`${API_BASE_URL}/api/bookings/${bookingId}`);
        const bookingData = bookingRes.data;
        setBooking(bookingData);

        //  flightId
        const flightId =
          bookingData.flightId && typeof bookingData.flightId === 'object'
            ? bookingData.flightId._id
            : bookingData.flightId;

        if (!flightId || typeof flightId !== 'string' || flightId.length !== 24) {
          console.error(' Invalid or missing flightId in booking data:', flightId);
          return;
        }

        //    
        const flightRes = await axios.get(`${API_BASE_URL}/api/flights/${flightId}`);
        setFlight(flightRes.data);
      } catch (error) {
        console.error('Error fetching data:', error.message);
      }
    };

    fetchData();
  }, [bookingId]);

  const handlePrint = () => {
    if (!printRef.current) return;
    const printContents = printRef.current.innerHTML;
    const originalContents = document.body.innerHTML;
    document.body.innerHTML = printContents;
    window.print();
    document.body.innerHTML = originalContents;
    window.location.reload();
  };

  if (!booking || !flight) return <p className="loading">Loading ticket...</p>;

  const extraFee = (booking.extraWeight / 5) * 10 || 0;
  const seatFee = booking.seatNumber ? 5 : 0;
  const petFee = booking.petDetails ? 25 : 0;
  const totalPaid = flight.price + extraFee + seatFee + petFee;

  return (
    <div className="ticket-container" ref={printRef}>
      <h2 className="ticket-title"> Official Flight Ticket</h2>
      <div className="ticket-header">
        <div className="ticket-logo">
          <img src="/logo.png" alt="Skybridge Flights Logo" className="ticket-logo-img" />
        </div>
        <div className="ticket-qr">
          <QRCodeCanvas
            value={`https://skybridgeflights.com/verify-booking/${booking._id}`}
            size={100}
          />
          <p className="verify-text">Scan to verify</p>
        </div>
      </div>

      <div className="ticket-body">
        <div className="ticket-section">
          <h4>Passenger Information</h4>
          <p><strong>Name:</strong> {booking.firstName} {booking.middleName || booking.fatherName || ''} {booking.lastName}</p>
          <p><strong>Email:</strong> {booking.email}</p>
          <p><strong>Passport Number:</strong> {booking.passport}</p>
          <p><strong>Date of Birth:</strong> {booking.birthDate}</p>
          <p><strong>Gender:</strong> {booking.gender}</p>
          <p><strong>Seat:</strong> {booking.seatNumber || 'Not assigned'}</p>
          <p><strong>Pet:</strong> {booking.petDetails ? 'Yes' : 'No'}</p>
        </div>

        <div className="ticket-section">
          <h4>Flight Information</h4>
          <p><strong>Airline:</strong> {flight.airline}</p>
          <p><strong>Flight Number:</strong> {flight.flightNumber}</p>
          <p><strong>From:</strong> {flight.from}</p>
          <p><strong>To:</strong> {flight.to}</p>
          <p><strong>Departure:</strong> {flight.departureTime}</p>
          <p><strong>Arrival:</strong> {flight.arrivalTime}</p>
          <p><strong>Travel Class:</strong> {flight.class}</p>
          <p><strong>Extra Baggage:</strong> {booking.extraWeight || 0} kg</p>
        </div>

        <div className="ticket-section">
          <h4>Payment Summary</h4>
          <p><strong>Base Price:</strong> {flight.price}</p>
          <p><strong>Extra Baggage Fee:</strong> {extraFee}</p>
          <p><strong>Seat Selection Fee:</strong> {seatFee}</p>
          <p><strong>Pet Fee:</strong> {petFee}</p>
          <hr />
          <p><strong>Total Paid:</strong> {totalPaid}</p>
          <p><strong>Payment Method:</strong> {booking.paymentMethod}</p>
        </div>
      </div>

      <div className="ticket-footer">
        <img src="/signature.png" alt="Signature" className="signature" />
        <p className="authority">Skybridge Flights Authority</p>
        <button className="print-btn no-print" onClick={handlePrint}>
           Print Ticket (PDF)
        </button>
      </div>
    </div>
  );
};

export default TicketPage;