// frontend/src/pages/PaymentPage.js
import React, { useEffect, useState } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import axios from 'axios';
import './PaymentPage.css';
import { API_BASE_URL } from '../apiConfig'; //    API 

const PaymentPage = () => {
  const { bookingId } = useParams();
  const navigate = useNavigate();
  const location = useLocation();
  const [booking, setBooking] = useState(null);
  const [flight, setFlight] = useState(null);
  const [isProcessing, setIsProcessing] = useState(false);

  useEffect(() => {
    const fetchBookingData = async () => {
      try {
        const res = await axios.get(`${API_BASE_URL}/api/bookings/${bookingId}`);
        setBooking(res.data);

        const flightId = res.data.flightId && typeof res.data.flightId === 'object'
          ? res.data.flightId._id
          : res.data.flightId;

        if (!flightId) {
          console.error('Missing flight ID');
          return;
        }

        const flightRes = await axios.get(`${API_BASE_URL}/api/flights/${flightId}`);
        setFlight(flightRes.data);
      } catch (err) {
        console.error('Failed to load booking or flight data:', err.message);
      }
    };

    fetchBookingData();
  }, [bookingId]);

  const handlePayment = () => {
    setIsProcessing(true);

    setTimeout(() => {
      navigate(`/ticket/${bookingId}`);
    }, 2500); // simulate payment processing
  };

  if (!booking || !flight) {
    return (
      <div className="payment-container">
        <h2>Processing Payment...</h2>
        <p>Please wait while we confirm your booking and generate your ticket.</p>
      </div>
    );
  }

  const extraFee = (booking.extraWeight / 5) * 10 || 0;
  const seatFee = booking.seatNumber ? 5 : 0;
  const petFee = booking.petDetails ? 25 : 0;
  const totalPrice = flight.price + extraFee + seatFee + petFee;

  return (
    <div className="payment-container">
      {!isProcessing ? (
        <div className="payment-box">
          <h2> Complete Payment</h2>
          <p><strong>Name:</strong> {booking.firstName} {booking.lastName}</p>
          <p><strong>Flight:</strong> {flight.from}  {flight.to}</p>
          <p><strong>Airline:</strong> {flight.airline || 'Not specified'}</p>
          <p><strong>Seat:</strong> {booking.seatNumber || 'Not selected'}</p>
          <p><strong>Total Price:</strong> {totalPrice}</p>
          <p><strong>Payment Method:</strong> {booking.paymentMethod}</p>
          <button className="pay-btn" onClick={handlePayment}>Pay Now</button>
        </div>
      ) : (
        <div className="payment-box processing">
          <h2> Processing Payment...</h2>
          <p>Please wait while we confirm your booking and generate your ticket.</p>
        </div>
      )}
    </div>
  );
};

export default PaymentPage;